import { useDispatch, useSelector } from "react-redux";
import Layout from "../components/Layout/Layout";
import styled from "styled-components";
import { TfiEye } from "react-icons/tfi";
import { AiOutlineCheckCircle } from "react-icons/ai";
import { AiFillCheckCircle } from "react-icons/ai";
import { BsChat } from "react-icons/bs";
import { Link } from "react-router-dom";
import { readQuestions } from "../redux/modules/questions";
import { useEffect } from "react";

function Question() {
  const dispatch = useDispatch();
  const { questions } = useSelector((state) => state.questions);

  useEffect(() => {
    dispatch(readQuestions());
  }, [dispatch]);

  return (
    <Layout>
      <PageTitle>Question</PageTitle>
      <ThreadGroup>
        <ThreadTitle>Question Thread</ThreadTitle>
        <Threadicon>
          <AiOutlineCheckCircle />
          <div>
            <TfiEye />
          </div>
          <div>
            <BsChat />
          </div>
        </Threadicon>
      </ThreadGroup>
      <Line />
      <ThreadContainer>
        {questions?.map((question) => {
          return (
            <QList key={question.questionId}>
              <QListTitle to={`/questions/${question.questionId}`}>
                <A>{question.title}</A>
              </QListTitle>
              <QListWriter>
                :
                <QListUserId>{question.nickname}</QListUserId>
              </QListWriter>
              <QListicon>
                {/* <AiOutlineCheckCircle/> */}
                <AiFillCheckCircle />
              </QListicon>
              <ViewCount>
                <p>{question.view}</p>
              </ViewCount>
              <AnswerCount>
                <p>{question.answerCount}</p>
              </AnswerCount>
            </QList>
          );
        })}
      </ThreadContainer>
    </Layout>
  );
}

export default Question;

//   
const PageTitle = styled.div`
  width: 1000px;
  margin: 0 auto;
  margin-top: 4rem;
  font-size: 4rem;
  font-weight: bold;
`;
//  
const ThreadTitle = styled.div`
  padding: 1rem;
  font-size: 1.5rem;
`;

//  
const ThreadGroup = styled.div`
  /* background-color: green; */
  width: 1000px;
  margin: 0 auto;
  margin-top: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
`;
//  
const Threadicon = styled.div`
  display: flex;
  margin-right: 4.5rem;
  gap: 3rem;
`;

//  
const Line = styled.div`
  width: 1000px;
  margin: 0 auto;
  border-bottom: 1px solid #e9ecef;
`;

//  
const ThreadContainer = styled.div`
  /* background-color: #20c997; */
  max-width: 1000px;
  width: 100%;
  margin: 0 auto;
  height: 600px;
  overflow-x: hidden;
  overflow-y: auto;
  &::-webkit-scrollbar {
    width: 0.125rem;
  }
  &::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: #ccc;
  }
`;

// 
const QList = styled.div`
  /* background-color: green; */
  width: 725px;
  padding: 2rem;
`;
//  
const QListTitle = styled(Link)`
  font-weight: bold;
  font-size: 1rem;
`;
//   
const A = styled.span`
  color: #7f7f7f;
  text-decoration-line: none;
  &:hover {
    color: #000;
  }
`;

// 
const QListWriter = styled.div`
  display: flex;
  font-weight: bold;
  margin-top: 0.5rem;
`;

//  ID
const QListUserId = styled.div`
  font-weight: bold;
  margin-left: 0.3rem;
  color: #0070c9;
`;

//  
const QListicon = styled.div`
  /* background-color: red; */
  width: 1rem;
  margin-left: 722px;
  margin-top: -2rem;
`;

//  
const ViewCount = styled.div`
  /* background-color: blue; */
  margin: 0 auto;
  width: 3rem;
  text-align: center;
  margin-left: 775px;
  margin-top: -1.2rem;
`;

//  
const AnswerCount = styled.div`
  /* background-color: blue; */
  margin: 0 auto;
  width: 3rem;
  text-align: center;
  margin-left: 845px;
  margin-top: -1.05rem;
`;
